/* eslint-disable @typescript-eslint/no-explicit-any */
import {createSlice} from '@reduxjs/toolkit';
import {NameSpace} from '../NameSpace';

import {LocalStorageUtil} from '../../utils/LocalStorageUtil';

type InitialState = {
    favorite: any[];
};

const initialState: InitialState = {
    favorite: LocalStorageUtil.getAuth()
        ? JSON.parse(localStorage.getItem(LocalStorageUtil.getAuth()!)!).favorite.map(
              (item: any) => item
          )
        : [],
};

export const favorite = createSlice({
    name: NameSpace.Auth,
    initialState,
    reducers: {
        addFavorite: (state, action) => {
            state.favorite = [...state.favorite, action.payload];
        },
        deleteFavorite: (state, action) => {
            state.favorite = [...state.favorite.filter((item) => item.id !== action.payload)];
        },
    },
});

export const {addFavorite, deleteFavorite} = favorite.actions;
